$clr-bg: #F9F7F1;
$clr-font-light: $clr-bg;

@mixin flex-center {
   display: flex;
   justify-content: center;
   align-items: center;
}

@mixin absolute-top {
   position: absolute;
   top: 0;
   left: 0;
}

@mixin absolute-center {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
}

@mixin absolute-bottom {
   position: absolute;
   bottom: 0;
   left: 0;
}

:root {
   --vh100: 100vh;
}